
import Swiper from 'vux/src/components/swiper/swiper.vue'

export default {
	data (){
		return {
      title_link: {
        title: '秒杀',
        link: '#/list_seckill'
      },
      list: [{
        url: 'javascript:',
        img: require('../assets/gz5e.jpg'),
      }, {
        url: 'javascript:',
        img: require('../assets/tejiaPro2.jpg'),
      }],
      index: 0,
      tab: [
        {
          title: '商品信息',
          status: true
        },
        {
          title: '购买记录',
          status: false
        },
        {
          title: '顾客评论',
          status: false
        },
        {
          title: '买家问答',
          status: false
        },
      ],
      details: {
        title: '2016新茶上市 西湖牌百年老茶树西湖龙井茶叶200g经典绿茶纸包',
        scorestar: 5,
        sold: 0,
        storage: 30,
      },
      product_info: [
        {
            status: true,
            list: [
              require('../assets/pro_detail.jpg'),
              require('../assets/pro_detail2.jpg'),
              require('../assets/pro_detail3.jpg'),
            ]
        },
        {
            status: false,
            list: [
              '内容','内容','内容','内容','内容'
            ]
        },
        {
            status: false,
            list: [
              '内容','内容','内容','内容','内容'
            ]
        },
        {
            status: false,
            list: [
              '内容','内容','内容','内容','内容'
            ]
        }
      ]
    }
	},
  methods: {

  },
  computed: {
    update (){
        console.log('msg='+ this.$route.query.msg + '&id='+ this.$route.query.id);
        if (this.$route.query.msg == 'groupprice') {
          this.title_link = {
            title: '团购',
            link: '#/list_groupprice?list=groupprice'
          }
        }else if(this.$route.query.msg == 'seckill'){
          this.title_link = {
            title: '秒杀',
            link: '#/list_groupprice?list=seckill'
          }
        }
    }
  },
  created (){
    this.update;
  },
  watch: {
      $route(){
         this.update;
      }
  },
  components: {
    Swiper
  },
  methods: {
    tabs (index){
        for (var i = 0; i < this.tab.length; i++) {
            this.tab[i].status = false;
            this.product_info[i].status = false;
        }
        this.product_info[index].status = true;
        this.tab[index].status = true;
    },
  }
}



